require('normalize.css/normalize.css');
import style from './App.scss';

import React from 'react';
import LocalDb from '../../tools/localDb';
import TodoHeader from './TodoHeader';
import TodoMain from './TodoMain';
import TodoFooter from './TodoFooter';


class App extends React.Component {

    constructor(props) {
        super(props);
        this.db = new LocalDb('React-Todos');
        this.state = {
            todos: this.db.get('todos') || [],
            isAllChecked: false
        };
    }

    /**
     * 
     */
    allChecked(){
        let isAllChecked = false;
        if(this.state.todos.every((todo) => todo.isDone)){
            isAllChecked = true;
        }
        this.setState({
            todos: this.state.todos,
            isAllChecked: isAllChecked
        });
    }

    /**
     * Header
     * @param todoItem
     */
    addTodo(todoItem){
        this.state.todos.push(todoItem);
        // this.allChecked();
        // todoitemisAllCheckedfalse
        this.setState({
            todos: this.state.todos
        });

        this.db.set('todos',this.state.todos);
    }

    /**
     * TodoItemFooter
     * @param index  
     * @param isDone  
     * @param isChangeAll 
     */
    changeTodoState(index, isDone, isChangeAll = false){
        if(isChangeAll){
            let todos = this.state.todos;
            todos.map((todo) => {
                todo.isDone = isDone;
                return todo;
            });

            this.setState({
                todos: todos,
                isAllChecked: isDone
            });
        }else {
            this.state.todos[index].isDone = isDone;
            this.allChecked();
        }
        this.db.set('todos',this.state.todos);
    }

    /**
     * TodoItem
     * @param index
     */
    deleteTodo(index){
        this.state.todos.splice(index,1);
        this.setState({
            todos: this.state.todos
        });
        this.db.set('todos',this.state.todos);
    }

    /**
     * Footer
     */
    clearDone(){
        let todos = this.state.todos.filter((todo) => !todo.isDone);
        this.setState({
            todos: todos,
            isAllChecked: false
        });
        this.db.set('todos',todos);
    }


    render() {
        let headerProps = {
            addTodo: this.addTodo.bind(this)
        };
        let mainProps = {
            deleteTodo: this.deleteTodo.bind(this),
            todos: this.state.todos,
            changeTodoState: this.changeTodoState.bind(this)
        };

        let footerProps = {
                todoCount: this.state.todos.length || 0,
                todoDoneCount: (this.state.todos.filter((todo)=>todo.isDone)).length,
                isAllChecked: this.state.isAllChecked,
                clearDone: this.clearDone.bind(this),
                changeTodoState: this.changeTodoState.bind(this)
        };

        return (
            <div className={`page ${style.color} `}>
                <div className={style.container}>
                    <div className={style.panel}>
                        <TodoHeader {...headerProps}/>
                        <TodoMain   {...mainProps}/>
                        <TodoFooter {...footerProps} />
                    </div>
                </div>
            </div>
        );
    }
}

App.defaultProps = {};

export default App;



