import  style from './index.scss';

import React from 'react';
import Header from './Header';
import Item from './Item';
import AddItemForm  from './AddItemForm';

const INIT_ITEMS = [
    {
        name: 'Todo item 1',
        color: 'red',
        checked: false,
        edit: false,
        id: 1
    },
    {
        name: 'Todo item 2',
        color: 'pink',
        checked: false,
        edit: false,
        id: 2
    },
    {
        name: 'Todo item 3',
        color: 'purple',
        checked: false,
        edit: false,
        id: 3
    }
];

var itemId = 4;

/**
 * id
 * @param value 
 * @param arr  
 * @param prop 
 * @returns {number} 
 */
function getIndex(value, arr, prop) {
    for(let i = 0; i < arr.length; i++) {
        if(arr[i][prop] === value) {
            return i;
        }
    }
    return -1; //to handle the case where the value doesn't exist
}


class App extends React.Component {
    constructor(props){
        super(props);
        this.onItemAdd = this.onItemAdd.bind(this);
        this.onItemEdit = this.onItemEdit.bind(this);
        this.onCheckedChecker = this.onCheckedChecker.bind(this);
        this.onMultiDelete = this.onMultiDelete.bind(this);
        this.onEditCheck = this.onEditCheck.bind(this);
        this.clickOutside = this.clickOutside.bind(this);
        this.state = {
            items: INIT_ITEMS,
            countChecked: false,
            checkedNum: 1
        };
    }

    /**
     * clickitem
     */
    componentDidMount(){
        window.addEventListener('click', this.clickOutside);
    }

    /**
     * 
     */
    componentWillUnmount(){
        window.removeEventListener('click',this.clickOutside);
    }

    /**
     *  addItemFormsubmit
     */
    onItemAdd(name, color){
        this.state.items.push({
            name: name,
            color: color,
            checked: false,
            edit: false,
            id: itemId
        });
        this.setState(this.state);
        itemId += 1;
    }

    /**
     *  editItemFormsubmit
     *  item
     */
    onItemEdit(name, color, id){
        let array = this.state.items;
        let index = getIndex(id, array, 'id'); //
        this.state.items[index].name = name;
        this.state.items[index].color = color;
        this.state.items[index].edit = false;
        this.setState(this.state);
    }

    /**
     * item checked checked
     * APPcountChecked  checkedNum
     */
    onCheckedChecker(checked, id){
        let array = this.state.items;
        let index = getIndex(id, array, 'id');  //item
        let counter = [];

        if(this.state.items[index].edit === true){
            this.state.items[index].edit = false;
        }
        this.state.items[index].checked = checked;
        this.setState(this.state);

        this.state.items.forEach(function(obj, index){
            if(obj.checked === true){
                counter.push(obj);
            }
        });
        if(counter.length > 0){
            this.state.countChecked = true;
        }else {
            this.state.countChecked = false;
        }
        this.state.checkedNum = counter.length;
    }

    /**
     * checkeditem
     */
    onMultiDelete (){
        let items = this.state.items;
        let keep = [];   //checked
        this.state.items.forEach(function(item, index){
            if(item.checked !== true){
                keep.push(items[index]);
            }
        });
        this.setState({
            items: keep,
            countChecked: false,
            checkedNum: 1
        });
    }

    /**
     * item
     * @param flag
     * @param id
     */
    onEditCheck(flag, id){

        let array = this.state.items;
        let index = getIndex(id, array, 'id');
        let counter = [];

        // item
        this.state.items.forEach(function(obj, index){
            obj.edit = false;
        });

        this.state.items[index].edit = flag; //flagtrue,item
        this.setState(this.state);

    }

    /**
     * itemitemeditfalse
     * editfalse, editItemForm
     */
    clickOutside(e){
        if(!this.refs.todoList.contains(e.target)){
            let shouldSetSate = false;
            this.state.items.forEach((item) =>{
                if(item.edit){
                    item.edit = false;
                    shouldSetSate = true;
                }

            });
            /* shouldSetSatesetState()*/
            if(shouldSetSate){
                this.setState(this.state);
            }
        }
    }

    render(){
        return (
            <div className={`page ${style.color}`} style={{alignItems: 'flex-start'}}>
                <div className={style.container}>
                    <Header checkedItemsFlag={this.state.countChecked} checkedNum={this.state.checkedNum} handleDeleteItems={this.onMultiDelete} />
                    <ul ref="todoList">
                        {this.state.items.map(function(item, index){
                            return (
                                <Item
                                    text={item.name}
                                    color={item.color}
                                    checked={item.checked}
                                    edit={item.edit}
                                    key={item.id}
                                    id={item.id}
                                    onCheckedCheck={this.onCheckedChecker}
                                    editFlag={this.onEditCheck}
                                    onEdit={this.onItemEdit}
                                />
                            );
                        }.bind(this))}
                    </ul>
                    <AddItemForm addItem={this.onItemAdd} />
                </div>
            </div>
        );
    }

}


export default App;



