import React from 'react';
import EditItemForm  from '../EditItemForm';
import style from './index.scss';

class Item extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            clicked: false,
            checked: false,
            editSwitch: false
        };
    }

    /**
     * item checked
     */
    handleChange(e){
        this.state.checked = e.target.checked;
        this.props.onCheckedCheck(this.state.checked, this.props.id);
        // this.setState(this.state);  // onEditSwitch
    }

    /**
     *  item
     */
    onEditSwitch(){
        this.state.editSwitch = true;
       // this.setState(this.state);  //itemEditItemFormAppsetState
        //this.props.editFlag(this.state.editSwitch, this.props.id);
        this.props.editFlag(true, this.props.id);
    }

    /** shouldComponentUpdate
     * shouldComponentUpdatesetStatesetStaterender
     * shouldComponentUpdatetruesetStaterender
     * 20item, onEditSwitchitemrender, 20 item
     * shouldComponentUpdateprops.edit2render
     * itemitem
     * 10item
     * : item,radio
     */
    shouldComponentUpdate(nextProps, nextState){
        if(this.props.edit !== nextProps.edit){
            return true;
        }
        if(this.props.checked !== nextProps.checked){
            return true;
        }
        return false;
    }

    render(){
        let colorClass = this.props.color ? this.props.color : '';
        let checkedClass = this.state.checked ? 'disabled' : '';
        let editProps = {
            editMode: this.props.edit,
            text: this.props.text,
            color: this.props.color,
            notifyEdits: this.props.onEdit,
            id: this.props.id
        };
        return (
            <li className={`${style.item} ${style[colorClass]} ${style[checkedClass]}`}>
                <div className={style.checkbox}>
                    <input type="checkbox" onChange={this.handleChange.bind(this)} />
                </div>

                <div className={style.text}>
                    <p onClick={this.onEditSwitch.bind(this)}>{this.props.text}</p>
                    <EditItemForm {...editProps}/>
                </div>
            </li>
        );
    }
}

Item.propTypes = {
    text: React.PropTypes.string.isRequired,      // item
    color: React.PropTypes.string,                // item
    checked: React.PropTypes.bool.isRequired,     // item
    edit: React.PropTypes.bool.isRequired,        // itemEditItemForm
    id: React.PropTypes.number.isRequired,        // itemid
    onCheckedCheck: React.PropTypes.func.isRequired,
    editFlag: React.PropTypes.func.isRequired
};

export default Item;


