import React from 'react';
import style from './index.scss';

class ColorSelector extends React.Component {

    constructor(props){
        super(props);
        this.state = {
            color: this.props.color
        };
    }

    /**
     * radioonChange
     * 1. this.state.color
     * 2. setState()checkedclass, 
     * 3. item
     * @param e
     */
    handleColorChange(e){
        //this.state.color = e.target.value;     // setState(this.state.color)shouldComponentUpdate
        this.setState({color: e.target.value});
        this.props.colorChange(e.target.value);  //e.target.valuethis.state.color
    }

    /**
     *  
     */
    shouldComponentUpdate(nextProps, nextState){
        if(nextState.color !== this.state.color){
            return true;
        }
        return false;
    }

    render(){
        let radio = this.props.colorList.map((color, index) => {
            let checkedClass =  (this.state.color == color.name)? 'checked' : '';
            return  <input type="radio" onChange={this.handleColorChange.bind(this)}
                           className={`${style[color.name]} ${style[checkedClass]}`}
                           name="selectedColor"
                           value={color.name}
                           key={index} />
        });
        return (
            <div className={style.colorSelector}>
                {radio}
            </div>
        );
    }
}

/**
 *  colorList: 
 *  colorChange 
 */
ColorSelector.propTypes = {
    colorList: React.PropTypes.array.isRequired,   // 
    colorChange: React.PropTypes.func.isRequired,  // 
    color: React.PropTypes.string.isRequired       // checkedaddedit
};

ColorSelector.defaultProps = {
    color: 'red',
    colorList:[
        {
            name: 'red',
            checked: true,
            id: 1
        },
        {
            name: 'pink',
            checked: false,
            id: 2
        },
        {
            name: 'purple',
            checked: false,
            id: 3
        },
        {
            name: 'blue',
            checked: false,
            id: 4
        },
        {
            name: 'green',
            checked: false,
            id: 5
        },
        {
            name: 'yellow',
            checked: false,
            id: 6
        }
    ]
};


export default ColorSelector;


