import React from 'react';
import style from './App.scss';

class TodoFooter extends React.Component {

    /**
     * 
     * @param e
     */
    handleAllState(e){
        this.props.changeTodoState(null, e.target.checked, true);
    }

    /**
     * todos
     */
    handleClick(e){
        this.props.clearDone();
        e.stopPropagation();  //
    }


    render(){
        return (
            <div className={`${style.clearfix} ${style.footer}`} >
                <input type="checkbox" checked={this.props.isAllChecked}
                       onChange={this.handleAllState.bind(this)} className={style.fl}/>
                <span className={style.fl}> {this.props.todoDoneCount}/{this.props.todoCount}</span>
                <button onClick={this.handleClick.bind(this)} className={style.fr}></button>
            </div>
        );
    }
}

export default TodoFooter;

